.login {
	margin-top: 5em;
	display: flex;
	justify-content: center;
	width: 100%;
}

.input {
	border: none;
	outline: none;
	border-radius: 5px;
	width: 300px;
	margin-top: .4em;
	height: 40px;
	transition: all .4s;
	-webkit-box-shadow: 1px 1px 5px 0px rgba(207, 207, 207, 1);
	-moz-box-shadow: 1px 1px 5px 0px rgba(207, 207, 207, 1);
	box-shadow: 1px 1px 5px 0px rgba(207, 207, 207, 1);

	padding: 1em;
}

.submit {
	border-radius: 5px;
	background: rgb(54, 62, 124);
	color: white;
	border: none;
	height: 45px;
	&:disabled {
		background: rgb(131, 131, 131);
		cursor: not-allowed;
	}
}

.pic {
	width: 65px;
	padding-bottom: 1em;
}
